.emitirNota .notaCredito {
  background-color: lightgreen;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.emitirNota .notaCredito .none {
  display: none !important;
}
.emitirNota .notaCredito .formulario__campo {
  display: flex;
  flex-direction: column;
}
.emitirNota .notaCredito .formulario__campo input, .emitirNota .notaCredito .formulario__campo select {
  width: 90%;
}
.emitirNota .notaCredito .arriba {
  display: grid;
  grid-template-columns: 2fr 1fr 1fr;
}
.emitirNota .notaCredito .arriba .buscar {
  display: flex;
}
.emitirNota .notaCredito .arriba .buscar #codigoC {
  width: 5rem;
}
.emitirNota .notaCredito .arriba .buscar .nombre {
  margin-left: 0.5rem;
  width: 55rem;
}
.emitirNota .notaCredito .medio {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
}
.emitirNota .notaCredito .abajo {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
}
.emitirNota .notaCredito .listaProductos {
  overflow: scroll;
  height: 35rem;
  border: 0.1rem solid black;
  background-color: white;
}
.emitirNota .notaCredito .listaProductos .table {
  border-collapse: collapse;
  width: 100%;
}
.emitirNota .notaCredito .listaProductos .table thead tr {
  background-color: #ffdfdf;
}
.emitirNota .notaCredito .listaProductos .table thead td {
  font-size: 2rem;
  border: 0.1rem solid black;
}
.emitirNota .notaCredito .listaProductos .table tbody td {
  border: 0.1rem solid black;
}
.emitirNota .notaCredito #codigo {
  padding: 1rem;
}
.emitirNota .notaCredito .total {
  display: flex;
  justify-content: space-around;
}
.emitirNota .notaCredito .total input {
  padding: 0.5rem;
}
.emitirNota .notaCredito .total #descuento {
  width: 15%;
}
.emitirNota .notaCredito .botones {
  width: 100%;
  display: flex;
  justify-content: space-around;
  padding-bottom: 1rem;
}
.emitirNota .notaCredito .botones button {
  border: none;
  border-radius: 7.5rem;
  padding: 1.5rem;
}
.emitirNota .notaCredito .botones button:hover {
  transform: scale(1.2);
  cursor: pointer;
}